import {
	mapState,
	mapMutations
} from 'vuex';
import bottomCopyright from '@/components/bottomCopyright.vue'
export default {
	components: {
		bottomCopyright
	},
	data() {
		return {
			title: 'Hello',
			account: '',
			passwd: '',
			abOc: 0,
			showTip: false,
		}
	},
	computed: {
		...mapState(['globalOpenid', 'userInfo', 'token', 'globalUserType'])
	},
	onLoad(options) {
		var that = this;
		if (options.uSerType) {
			that.abOc = options.uSerType;
		}
		that.setGlobalUserType(that.abOc);
		let isFinish = uni.getStorageSync('isDone');
		if (isFinish === 0) {
			that.showTip = true;
			that.setGlobalUserType(1);
		} else {
			that.showTip = false;
		}

	},
	onShow() {},
	methods: {
		...mapMutations(['setGlobalUserInfo', 'setGlobalOpenid', 'setglobalWXuserInfo', 'setToken', 'setGlobalUserType']),
		// 用户协议
		navAboutUs(text) {
			uni.navigateTo({
				url: '/pages/public/aboutUs/aboutUs?title=' + text
			})
		},
		go_forget() {
			uni.navigateTo({
				url: '../../pages/forget/forget'
			})
		},
		go_register() {
			uni.navigateTo({
				url: '../../pages/register/register'
			})
		},
		inputAccount(e) {
			this.account = e.detail.value;
		},
		inputPasswd(e) {
			this.passwd = e.detail.value;
		},
		// 检查本地信息并且自动登录
		checkLocalInfo() {
			var that = this;
			if (uni.getStorageSync('localUserInfo')) {
				that.setGlobalUserInfo(uni.getStorageSync('localUserInfo'));
				that.setToken(uni.getStorageSync('token'));
				that.setGlobalOpenid(uni.getStorageSync('openid'));
				that.setglobalWXuserInfo(uni.getStorageSync('WXuserInfo'));
				that.userLoginCheck();
				uni.hideLoading();
			} else {
				setTimeout(() => {
					uni.showToast({
						title: '本地无信息，请登录',
						icon: 'none'
					});
				}, 800)
				uni.hideLoading();
			}
		},
		// 微信授权登录
		wxGetUserInfo: function(res) {
			var that = this;
			if (!res.detail.iv) {
				return false;
			} else {
				uni.getProvider({
					service: 'oauth',
					success: function(res) {
						if (res.provider[0] === 'weixin') {
							uni.login({
								provider: 'weixin',
								success: function(loginRes) {
									// 拿code
									uni.getUserInfo({
										provider: 'weixin',
										success: function(infoRes) {
											// 拿到微信用户信息
											that.setglobalWXuserInfo(infoRes.userInfo)
											uni.setStorageSync('WXuserInfo', infoRes.userInfo)
										}
									});
									uni.request({
										url: that.global_baseUrl + '/index.php/wxlogin',
										header: {
											'content-type': 'application/json'
										},
										data: {
											appid: 'wxf919a36c07b7caab',
											secret: '6cd94792bdc344e02ddff814c39fa3c6',
											code: loginRes.code
										},
										method: 'POST',
										success(res) {
											res.data = JSON.parse(res.data);
											that.setGlobalOpenid(res.data.openid);
											uni.setStorageSync('openid', res.data.openid)
											//登录检验
											that.userLoginCheck();
										}
									})
									uni.showLoading({
										title: "登录中..."
									})
									setTimeout(() => {
										uni.hideLoading();
									}, 700)
								}
							});
						}
					}
				});
			}
		},
		// 学生登录校验
		async userLoginCheck() {
			var that = this;
			var tempApi = 'login';
			if (that.globalUserType == 1) {
				tempApi = 'shoplogin'
			}
			var tempData = {
				wei: this.globalOpenid,
				token: that.token
			}
			if (uni.getStorageSync('token')) {
				tempData.token = uni.getStorageSync('token')
			}
			uni.request({
				url: this.global_baseUrl + '/index.php/' + tempApi,
				data: tempData,
				method: "POST",
				success: (res) => {
					if (res.data.code == '404') {
						uni.showModal({
							title: '提示',
							content: '当前您的信息为空，前往绑定',
							success: function(res) {
								if (res.confirm) {
									// 微信号为空 ，前去绑定
									// 跳转绑定界面
									if (that.globalUserType == 0) {
										uni.navigateTo({
											url: '/pages/public/infoBind/infoBind'
										})
									} else {
										uni.navigateTo({
											url: '/pages/public/infoBindBusiness/infoBindBusiness'
										})
									}

								} else if (res.cancel) {;
								}
							}
						});
					} else if (res.data.code == '406') {
						uni.showToast({
							title: "审核未通过!",
							icon: 'none'
						})
					} else if (res.data.code == '407') {
						that.showTip = true;
						uni.showToast({
							title: "审核中,需1-2工作日",
							icon: 'none'
						})
					} else if (res.data.code == '200') {
						that.showTip = false;
						uni.setStorage({
							key: 'localUserInfo',
							data: res.data.data,
							success: function() {
								that.setGlobalUserInfo(res.data.data)
								that.setToken(res.data.data.token);
								uni.clearStorageSync('isDone')
								uni.showToast({
									title: "登录成功!",
									icon: 'success'
								})
								setTimeout(() => {
									uni.switchTab({
										url: '/pages/home/home'
									})
								}, 800)
							}
						});
					} else {
						uni.showToast({
							title: "登录失败，请检查！",
							icon: 'none'
						})
					}
				}
			});
		},

	}
}
